
$colors: (
  "white": #fff,
  "green": #468217,
  "red": #d65555,
);

svg {
  flex-shrink: 0;
  @each $name, $color in $colors {
    &.#{$name} {
      circle {
        stroke: $color;
      }

      path {
        fill: $color;
      }

      @media (max-width: 768px) {
        width: 24px;
        height: 24px;
      }
    }
  }

  &.red {
    .vertical {
      display: none;
    }
  }

  &.white {
    @media (max-width: 768px) {
      width: 30px;
      height: 30px;
    }
    filter: drop-shadow(0px 2px 5px rgb(255 255 255 / 0));
    transition: all 0.8s;

    &:hover {
      filter: drop-shadow(0px 2px 5px rgb(255 255 255 / 0.6));
      transform: scale(1.05);
    }
  }
}
