@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
  list-style-type: none;
}

.will-change--active {
  will-change: auto;
}

body {
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1610px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 992px) {
  .container {
    max-width: 740px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 560px;
  }
}
@media (max-width: 576px) {
  .container {
    padding: 0 10px;
  }
}

div.title {
  display: flex;
  gap: 18px;
  flex-direction: column;
  margin-bottom: 45px;
}
@media (max-width: 768px) {
  div.title {
    margin-bottom: 24px;
    gap: 8px;
  }
}
div.title h2 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 67px;
  color: #2b2b2b;
  text-align: center;
}
@media (max-width: 1280px) {
  div.title h2 {
    font-size: 52px;
  }
}
@media (max-width: 992px) {
  div.title h2 {
    font-size: 45px;
    line-height: 110%;
  }
}
@media (max-width: 768px) {
  div.title h2 {
    font-size: 28px;
    line-height: 33px;
  }
}
div.title p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 115%;
  text-align: center;
  color: #2b2b2b;
}
@media (max-width: 1280px) {
  div.title p {
    font-size: 32px;
  }
}
@media (max-width: 992px) {
  div.title p {
    font-size: 27px;
    line-height: 110%;
  }
}
@media (max-width: 768px) {
  div.title p {
    font-size: 22px;
    line-height: 26px;
  }
}

.vita-input {
  display: block;
  padding: 16px 23px;
  border: 2px solid #cecece;
  border-radius: 10px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 31px;
  color: #2b2b2b;
  width: 100%;
  transition: all 0.3s ease;
}
.vita-input::placeholder {
  color: #2b2b2b;
  opacity: 1;
}
@media (max-width: 576px) {
  .vita-input {
    padding: 12px 14px;
    font-size: 14px;
    line-height: 16px;
    border-radius: 6px;
  }
}
.vita-input:hover, .vita-input:focus {
  transform: translateY(-1px);
  border: 2px solid rgb(86, 77, 77);
  box-shadow: 0 0 7px 1px rgba(86, 77, 77, 0.45);
}
.vita-input--error {
  cursor: default;
  border: 2px solid rgba(214, 85, 85, 0.9) !important;
  box-shadow: 0 0 7px 1px rgba(214, 85, 85, 0.45) !important;
}

.image-lazy {
  opacity: 0;
  transform: scale(0.9);
  transition: all 0.8s;
}
.image-lazy--loaded {
  opacity: 1;
  transform: scale(1);
}

.gorizontal-line {
  height: 2px;
  width: 100%;
  background: #cecece;
}

::-webkit-scrollbar {
  width: 12px;
  background-color: rgba(239, 239, 239, 0.6);
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(57, 57, 57, 0.35);
  z-index: 1;
  cursor: pointer;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(57, 57, 57, 0.45);
}
::-webkit-scrollbar-thumb:active {
  background: rgba(57, 57, 57, 0.6);
}

::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: rgba(239, 239, 239, 0.2);
}

*::selection {
  background: rgba(206, 205, 205, 0.8);
  color: #2b2b2b;
}

.fancybox__content .is-close {
  top: 0 !important;
  color: #2b2b2b !important;
  outline: none !important;
}
.fancybox__content .is-close svg {
  width: 18px !important;
  height: 18px !important;
  filter: none !important;
}

.checkbox {
  position: relative;
  display: flex;
  align-items: center;
  column-gap: 10px;
  width: max-content;
  max-width: 100%;
  cursor: pointer;
}
@media (max-width: 575px) {
  .checkbox {
    order: 1;
  }
}
.checkbox input {
  position: absolute;
  visibility: hidden;
  width: 1px;
  height: 1px;
}
.checkbox div.box {
  width: 26px;
  height: 26px;
  border: 2px solid #cecece;
  border-radius: 2px;
  background-color: white;
  transition: 0.3s all;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
@media (max-width: 576px) {
  .checkbox div.box {
    width: 20px;
    height: 20px;
  }
  .checkbox div.box svg {
    width: 14px;
    height: 12px;
  }
}
.checkbox div.box svg path {
  display: none;
}
.checkbox span {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 32px;
  color: #2b2b2b;
}
@media (max-width: 575px) {
  .checkbox span {
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
  }
}
.checkbox:hover input[type=checkbox]:not(:checked) + div {
  border-color: #468217;
  box-shadow: 0 0 4px 1px rgba(70, 130, 23, 0.3);
}
.checkbox input[type=checkbox]:checked + div {
  background-color: #468217;
  border: 2px solid #468217;
}
.checkbox input[type=checkbox]:checked + div svg path {
  display: block;
  stroke-dasharray: 700;
  animation: dash 2.3s linear alternate 1;
}

@keyframes dash {
  0% {
    stroke-dashoffset: 822;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
