.page-dostavka {
  background-color: #fff;
  font-family: "GothaProReg", Arial, sans-serif;
  font-size: 14px;
  line-height: normal;
  color: #000000;
  padding: 0;
  margin: 0;
  /*Section-hero*/
}
.page-dostavka form {
  max-width: 520px;
}
.page-dostavka h1 {
  color: #2b2b2b;
  font-weight: 700;
  font-size: 70px;
  line-height: 90% !important;
  text-align: center;
}
.page-dostavka h1 .block {
  display: inline-flex;
  align-items: center;
}
.page-dostavka h1 .quotes {
  font-weight: 500;
  font-family: "Roboto";
  font-size: 40px;
  line-height: 115%;
}
@media (max-width: 576px) {
  .page-dostavka h1 .quotes {
    font-size: 17px;
  }
}
.page-dostavka h1 .title {
  font-family: "Phudu";
  font-style: normal;
  font-weight: 700;
  font-size: 100px;
  line-height: 67px;
  margin-bottom: 30px;
}
@media (max-width: 992px) {
  .page-dostavka h1 .title {
    font-size: 50px;
    margin-bottom: 0;
  }
}
@media (max-width: 576px) {
  .page-dostavka h1 .title {
    font-size: 24px;
    margin-bottom: 0;
    line-height: 115%;
  }
}
@media (max-width: 992px) {
  .page-dostavka h1 {
    font-size: 40px;
  }
}
@media (max-width: 576px) {
  .page-dostavka h1 {
    font-size: 24px;
  }
}
.page-dostavka * {
  box-sizing: border-box;
}
.page-dostavka a {
  text-decoration: none;
}
.page-dostavka ul,
.page-dostavka ol {
  list-style: none;
}
.page-dostavka .text-center-container {
  margin: 0 auto;
  max-width: 1150px;
  width: 100%;
}
@media (max-width: 992px) {
  .page-dostavka .section-send__form {
    margin-left: auto;
    margin-right: auto;
  }
}
.page-dostavka .content-container {
  width: 100%;
  max-width: 1170px;
  height: auto;
  margin: 0 auto;
}
@media (max-width: 1279px) {
  .page-dostavka .content-container {
    max-width: 950px;
  }
}
@media (max-width: 979px) {
  .page-dostavka .content-container {
    max-width: 750px;
  }
}
@media (max-width: 767px) {
  .page-dostavka .content-container {
    width: 90%;
  }
}
@media (max-width: 479px) {
  .page-dostavka .content-container {
    min-width: 300px;
  }
}
.page-dostavka .columns {
  display: inline-block;
  float: none;
  width: 100%;
}
.page-dostavka .main-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 55px;
  padding: 15px 24px;
  width: auto;
  height: 55px;
  border-radius: 3px;
  box-shadow: 3px 5px 9px 0 rgba(0, 0, 0, 0.3);
  text-decoration: none;
  text-shadow: none;
  border: none;
  font-family: "CenturyGothic-Bold";
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  background: #ffc600;
  color: #1d2126;
  cursor: pointer;
  transition: 0.4s all;
}
.page-dostavka .main-btn:hover {
  background: #e2bc85;
  color: #ffffff;
}
.page-dostavka .header {
  padding: 45px 0 20px;
  background: #ffffff;
}
@media (max-width: 1199px) {
  .page-dostavka .header {
    height: auto;
  }
}
@media (max-width: 991px) {
  .page-dostavka .header {
    padding: 18px 0 0;
  }
}
.page-dostavka .header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .page-dostavka .header__content {
    margin-bottom: 10px;
  }
}
.page-dostavka .logo {
  display: flex;
  align-items: center;
}
@media (max-width: 1549px) {
  .page-dostavka .logo {
    flex-direction: row;
  }
}
.page-dostavka .logo p {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #323232;
  padding: 4px 0;
  margin-bottom: 0;
  margin-left: 25px;
  margin-top: 0;
}
@media (max-width: 767px) {
  .page-dostavka .logo p {
    margin-left: 18px;
  }
}
@media (max-width: 576px) {
  .page-dostavka .logo p {
    display: none;
  }
}
@media (max-width: 576px) {
  .page-dostavka .logo svg {
    width: 75px;
  }
}
.page-dostavka .phone-block .phone {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 30px;
  line-height: 39px;
  /* identical to box height */
  color: #323232;
  transition: 0.3s all;
}
@media (max-width: 576px) {
  .page-dostavka .phone-block .phone {
    font-size: 20px;
    line-height: 26px;
  }
}
.page-dostavka .phone-block .phone:hover {
  color: #e2bc85;
}
.page-dostavka .breadcrumbs {
  margin-bottom: 10px;
  display: flex;
  font-family: inherit;
  font-size: 14px;
  line-height: 14px;
  color: #cecece;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  padding-left: 0;
}
.page-dostavka .breadcrumbs li:not(:first-child) {
  padding-left: 8px;
}
.page-dostavka .breadcrumbs li a {
  color: #cecece;
  padding-right: 5px;
  transition: 0.3s all;
}
.page-dostavka .breadcrumbs li a:hover {
  color: #e2bc85;
}
.page-dostavka .section-hero {
  font-family: "Roboto";
  padding-top: 70px;
  padding-bottom: 50px;
}
@media (max-width: 767px) {
  .page-dostavka .section-hero {
    padding-top: 50px;
    padding-bottom: 30px;
  }
}
.page-dostavka .section-hero__heading {
  font-size: 65px;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 70px 0;
}
@media (max-width: 767px) {
  .page-dostavka .section-hero__heading {
    font-size: 26px;
    margin-bottom: 30px;
  }
}
.page-dostavka .section-hero__description:not(:last-child) {
  line-height: 22px;
  margin-bottom: 20px;
}
.page-dostavka .section-hero__list {
  margin-bottom: 20px;
  margin-top: 0;
}
.page-dostavka .section-hero__item:not(:last-child) {
  margin-bottom: 7px;
}
.page-dostavka .section-hero__item {
  position: relative;
  padding-left: 28px;
}
.page-dostavka .section-hero__item::before {
  content: "✓";
  color: #e2bc85;
  width: 16px;
  height: 16px;
  display: block;
  position: absolute;
  left: 0;
  top: 0px;
}
.page-dostavka #header {
  position: fixed;
  width: 100%;
  background-color: white;
  z-index: 10;
}
.page-dostavka #header .header-block .content-container {
  margin-bottom: 0;
}
@media (max-width: 767px) {
  .page-dostavka #header .header-block {
    margin-bottom: 10px;
  }
  .page-dostavka .header__content {
    gap: 5px !important;
  }
}
.page-dostavka main {
  padding-top: 110px;
}
@media (max-width: 992px) {
  .page-dostavka main {
    padding-top: 90px;
  }
}
@media (max-width: 576px) {
  .page-dostavka main {
    padding-top: 50px;
  }
}