.swiper-nav__container {
  z-index: 5;
}
.swiper-nav__container .swiper-btn__next,
.swiper-nav__container .swiper-btn__prev {
  cursor: pointer;
}
.swiper-nav__container .swiper-btn__next svg,
.swiper-nav__container .swiper-btn__prev svg {
  transition: all 0.6s ease;
}
.swiper-nav__container .swiper-btn__prev:active svg {
  transform: translateY(3px);
}
.swiper-nav__container .swiper-btn__next svg {
  transform: rotate(180deg);
}
.swiper-nav__container .swiper-btn__next:active svg {
  transform: rotate(180deg) translateY(-3px);
}