@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
article.characteristic-item .characteristic-item-container {
  background: #ffffff;
  border: 3px solid #ececec;
  box-shadow: 15px 15px 40px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
}

article.characteristic-item {
  padding-bottom: 100px;
}
@media (max-width: 992px) {
  article.characteristic-item {
    padding-bottom: 80px;
  }
}
@media (max-width: 768px) {
  article.characteristic-item {
    padding-bottom: 60px;
  }
}

article.characteristic-item .container {
  height: 100%;
}
@media (min-width: 993px) {
  article.characteristic-item .container .characteristic-wraper {
    height: 100%;
  }
}
@media (max-width: 992px) {
  article.characteristic-item {
    height: auto;
    padding-bottom: 80px;
  }
}
@media (max-width: 768px) {
  article.characteristic-item {
    padding-bottom: 60px;
  }
}
article.characteristic-item .characteristic-item-container {
  height: 100%;
  padding: 70px;
}
@media (max-width: 1366px) {
  article.characteristic-item .characteristic-item-container {
    padding: 40px;
  }
}
@media (max-width: 1280px) {
  article.characteristic-item .characteristic-item-container {
    padding: 30px;
  }
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-item-container {
    padding: 34px 34px;
  }
}
@media (max-width: 576px) {
  article.characteristic-item .characteristic-item-container {
    padding: 24px 14px;
  }
}
article.characteristic-item .characteristic-wraper {
  display: grid;
  grid-template-columns: 1fr 2px 1fr;
  grid-template-rows: auto 1fr;
  column-gap: 50px;
}
@media (max-width: 1366px) {
  article.characteristic-item .characteristic-wraper {
    column-gap: 40px;
  }
}
@media (max-width: 1280px) {
  article.characteristic-item .characteristic-wraper {
    align-items: center;
    column-gap: 30px;
  }
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto 11px auto;
    gap: 32px;
    max-width: 520px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (max-width: 576px) {
  article.characteristic-item .characteristic-wraper {
    grid-template-rows: auto auto 11px auto;
  }
}
article.characteristic-item .characteristic-wraper__media {
  grid-row: 1/3;
  grid-column: 3/4;
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper__media {
    grid-row: 2/3;
    grid-column: 1/2;
    height: 100%;
  }
}
article.characteristic-item .characteristic-wraper__media .media--img img {
  width: 100%;
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper__media .media--img img {
    max-height: 100%;
    max-width: 100%;
    width: auto;
    display: block;
    margin: 0 auto;
  }
}
article.characteristic-item .characteristic-wraper__media video {
  height: 100%;
  display: block;
  width: 100%;
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper__media video {
    width: 95%;
    margin: 0 auto;
  }
}
article.characteristic-item .characteristic-wraper__title {
  grid-row: 1/2;
  grid-column: 1/2;
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper__title {
    align-self: flex-start;
  }
}
article.characteristic-item .characteristic-wraper__title h2 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  color: #2b2b2b;
  font-size: 60px;
  margin-bottom: 30px;
  font-size: calc(0.021574973 * 100vw + 18.5760517799px);
}
@media (min-width: 1921px) {
  article.characteristic-item .characteristic-wraper__title h2 {
    font-size: 60px;
  }
}
@media (max-width: 993px) {
  article.characteristic-item .characteristic-wraper__title h2 {
    font-size: 40px;
  }
}
@media (max-width: 1366px) {
  article.characteristic-item .characteristic-wraper__title h2 {
    margin-bottom: 22px;
  }
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper__title h2 {
    text-align: center;
    margin-bottom: 8px;
  }
}
@media (max-width: 768px) {
  article.characteristic-item .characteristic-wraper__title h2 {
    font-weight: 700;
    font-size: 28px;
    line-height: 100%;
  }
}
@media (max-width: 576px) {
  article.characteristic-item .characteristic-wraper__title h2 {
    width: calc(100% + 6px);
    transform: translateX(-3px);
  }
}
article.characteristic-item .characteristic-wraper__title p.subtitle {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  line-height: 115%;
  color: #2b2b2b;
  margin-bottom: 30px;
  font-size: 40px;
  font-size: calc(0.0172599784 * 100vw + 6.8608414239px);
}
@media (min-width: 1921px) {
  article.characteristic-item .characteristic-wraper__title p.subtitle {
    font-size: 40px;
  }
}
@media (max-width: 993px) {
  article.characteristic-item .characteristic-wraper__title p.subtitle {
    font-size: 24px;
  }
}
@media (max-width: 1366px) {
  article.characteristic-item .characteristic-wraper__title p.subtitle {
    margin-bottom: 22px;
  }
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper__title p.subtitle {
    margin-bottom: 0;
    width: 80%;
    text-align: center;
  }
}
@media (max-width: 768px) {
  article.characteristic-item .characteristic-wraper__title p.subtitle {
    font-weight: 500;
    font-size: 22px;
    line-height: 26px;
  }
}
@media (max-width: 576px) {
  article.characteristic-item .characteristic-wraper__title p.subtitle {
    width: calc(100% + 18px);
    transform: translateX(-9px);
  }
}
article.characteristic-item .characteristic-wraper__content {
  grid-row: 2/3;
  grid-column: 1/2;
  align-self: start;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (max-width: 1366px) {
  article.characteristic-item .characteristic-wraper__content {
    gap: 20px;
  }
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper__content {
    grid-row: 4/5;
    grid-column: 1/2;
  }
}
article.characteristic-item .characteristic-wraper__content p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  line-height: 115%;
  color: #2b2b2b;
  font-size: 22px;
  font-size: calc(0.0043149946 * 100vw + 13.715210356px);
}
@media (min-width: 1921px) {
  article.characteristic-item .characteristic-wraper__content p {
    font-size: 22px;
  }
}
@media (max-width: 993px) {
  article.characteristic-item .characteristic-wraper__content p {
    font-size: 18px;
  }
}
@media (max-width: 768px) {
  article.characteristic-item .characteristic-wraper__content p {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
  }
}
article.characteristic-item .characteristic-wraper__content p span {
  font-weight: 700;
}
article.characteristic-item .characteristic-wraper__line {
  grid-row: 1/3;
  grid-column: 2/3;
  height: 100%;
  width: 100%;
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper__line {
    grid-row: 3/4;
    grid-column: 1/2;
    margin: 5px auto;
  }
}
article.characteristic-item .characteristic-wraper__line .line {
  height: 100%;
  background: #cecece;
}
@media (max-width: 992px) {
  article.characteristic-item .characteristic-wraper__line .line {
    height: 2px;
  }
}
@media (min-width: 993px) {
  article.characteristic-item .characteristic-wraper.reverse .characteristic-wraper__media {
    grid-row: 1/3;
    grid-column: 1/2;
  }
  article.characteristic-item .characteristic-wraper.reverse .characteristic-wraper__content {
    grid-row: 2/3;
    grid-column: 3/4;
  }
  article.characteristic-item .characteristic-wraper.reverse .characteristic-wraper__title {
    grid-row: 1/2;
    grid-column: 3/4;
  }
}