header {
  position: relative;
  overflow: hidden;
  max-width: 2560px;
  margin: 0 auto 100px;
}
@media (max-width: 992px) {
  header {
    margin-bottom: 30px;
  }
}
header.webp {
  background: url("../images/main-bg.webp") no-repeat;
  background-position: top 0 right 0;
  background-size: cover;
}
@media (max-width: 1366px) {
  header.webp {
    background-position: top 0 right -140px;
  }
}
@media (max-width: 992px) {
  header.webp {
    background: url("../images/main-bg-mobile.webp") no-repeat;
    background-position: center 0;
    background-size: 1366px;
  }
}
@media (max-width: 768px) {
  header.webp {
    background-size: 926px;
  }
}
header.nowebp {
  background: url("../images/main-bg.png") no-repeat;
  background-position: top 0 right 0;
  background-size: cover;
}
@media (max-width: 1366px) {
  header.nowebp {
    background-position: top 0 right -140px;
  }
}
@media (max-width: 992px) {
  header.nowebp {
    background: url("../images/main-bg-mobile.png") no-repeat;
    background-position: center 0;
    background-size: 1366px;
  }
}
@media (max-width: 768px) {
  header.nowebp {
    background-size: 926px;
  }
}

#component-modal {
  padding: 29px 10px 7px;
  background: #468217;
  border-radius: 16px;
  max-width: 290px;
  width: 100%;
  font-family: "Roboto";
  font-style: normal;
  color: white;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
#component-modal span {
  font-weight: 700;
  font-size: 20px;
  line-height: 31px;
  margin-bottom: 8px;
  display: block;
}