@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
.header-top {
  position: fixed;
  background-color: rgba(43, 43, 43, 0);
  width: 100%;
  box-shadow: 0 7px 9px 1px rgba(0, 0, 0, 0);
  transition: all 0.4s ease-in-out;
  padding: 0 20px;
  z-index: 20;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 576px) {
  .header-top {
    padding: 0 10px;
  }
}
.header-top .container {
  max-width: 1610px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 992px) {
  .header-top .container {
    max-width: 1610px;
  }
}
@media (max-width: 576px) {
  .header-top .container {
    padding: 0 10px;
  }
}
.header-top--scroll {
  background-color: #2b2b2b;
  box-shadow: 0 7px 9px 1px rgba(43, 43, 43, 0.3);
}
.header-top--scroll .header-top__line {
  border-bottom: 1px solid rgba(43, 43, 43, 0);
}
.header-top__line {
  transition: all 0.4s ease-in-out 0.2s;
  max-width: 1804px;
  border-bottom: 1px solid #fff;
  margin: 0 auto;
}
.header-top__line .container {
  padding: 0;
}
.header-top__wrap {
  display: flex;
  align-items: center;
  column-gap: 16px;
  justify-content: space-between;
  height: 90px;
}
@media (max-width: 576px) {
  .header-top__wrap {
    height: 60px;
  }
}
.header-top__logo {
  display: flex;
  align-items: center;
  column-gap: 30px;
  cursor: pointer;
}
.header-top__logo p {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #fff;
}
@media (max-width: 576px) {
  .header-top__logo p {
    display: none;
  }
}
.header-top__contact {
  display: flex;
  align-items: center;
  column-gap: 30px;
}
@media (max-width: 1280px) {
  .header-top__contact {
    gap: 20px;
  }
}
@media (max-width: 768px) {
  .header-top__contact {
    gap: 14px;
  }
}
.header-top__contact-phone {
  display: block;
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 29px;
  color: #fff;
  white-space: nowrap;
  transition: all 0.4s;
}
.header-top__contact-phone:hover {
  color: #e2bc85;
}
@media (max-width: 1366px) {
  .header-top__contact-phone {
    display: none;
  }
}
.header-top__contact-button {
  cursor: pointer;
  display: flex;
  align-items: center;
  column-gap: 18px;
  height: 44px;
  width: 194px;
  justify-content: center;
  border: 1px solid #fff;
  border-radius: 22px;
  transition: all 0.3s;
  box-shadow: 0 10px 30px rgba(255, 255, 255, 0);
}
@media (max-width: 768px) {
  .header-top__contact-button {
    width: 44px;
  }
}
@media (max-width: 576px) {
  .header-top__contact-button {
    width: 36px;
    height: 36px;
  }
}
.header-top__contact-button span {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  color: #fff;
  white-space: nowrap;
}
@media (max-width: 768px) {
  .header-top__contact-button span {
    display: none;
  }
}
.header-top__contact-button svg g path,
.header-top__contact-button span {
  transition: all 0.3s;
}
.header-top__contact-button:hover, .header-top__contact-button:focus {
  background: #468217;
  transform: translateY(-1px) scale(1.03);
  border: 1px solid #468217;
  box-shadow: 0 10px 30px rgba(70, 130, 23, 0.22);
}
.header-top__contact-button:hover svg g path,
.header-top__contact-button:hover span, .header-top__contact-button:focus svg g path,
.header-top__contact-button:focus span {
  color: #fff;
}
.header-top__contact-button:active {
  transform: translateY(1px);
}
.header-top__nav .menu-list {
  display: flex;
  align-items: center;
  column-gap: 35px;
}
@media (max-width: 1280px) {
  .header-top__nav .menu-list {
    flex-direction: column;
    align-items: start;
    gap: 10px;
    position: absolute;
    top: 0;
    z-index: 10;
    background: #fff;
    width: 280px;
    box-sizing: border-box;
    padding: 65px 20px 20px;
    right: 10px;
    transform: translateY(-100%);
    opacity: 0;
    transition: transform 0.45s 0.1s, opacity 0.25s;
  }
}
@media (max-width: 576px) {
  .header-top__nav .menu-list {
    top: -5px;
    right: 5px;
  }
}
.header-top__nav .menu-list--open {
  transform: translateY(10px);
  opacity: 1;
  transition: transform 0.4s, opacity 0.3s 0.2s;
}
.header-top__nav .menu-list li p {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 18px;
  color: #fff;
  transition: all 0.4s;
  cursor: pointer;
}
.header-top__nav .menu-list li p:hover {
  color: #e2bc85;
}
@media (max-width: 1280px) {
  .header-top__nav .menu-list li p {
    color: #2b2b2b;
    padding: 5px 0;
    font-size: 18px;
  }
}
.header-top .burger {
  position: relative;
  width: 40px;
  height: 14px;
}
.header-top .burger-container {
  display: block;
  height: 44px;
  width: 44px;
  border-radius: 22px;
  border: 1px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 20;
  transition: all 0.3s;
}
@media (min-width: 1281px) {
  .header-top .burger-container {
    display: none;
  }
}
@media (max-width: 576px) {
  .header-top .burger-container {
    width: 36px;
    height: 36px;
  }
}
.header-top .burger-container:hover, .header-top .burger-container:focus {
  transform: translateY(-1px) scale(1.03);
}
.header-top .burger-container:active {
  transform: translateY(1px);
}
.header-top .burger-container--open {
  border: 1px solid #2b2b2b;
}
.header-top .burger span {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 2px;
  border-radius: 0;
  background-color: #fff;
  transition: 0.4s all;
}
.header-top .burger span:nth-child(1) {
  top: 0;
}
.header-top .burger span:nth-child(2), .header-top .burger span:nth-child(3) {
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.header-top .burger span:nth-child(4) {
  top: 100%;
  transform: translateX(-50%) translateY(-50%);
}
.header-top .burger--open span {
  background-color: #2b2b2b;
}
.header-top .burger--open span:nth-child(1) {
  transition: 0.2s all;
  opacity: 0;
}
.header-top .burger--open span:nth-child(2) {
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
}
@media (max-width: 479px) {
  .header-top .burger--open span:nth-child(2) {
    width: 24px;
  }
}
.header-top .burger--open span:nth-child(3) {
  transform: translateX(-50%) translateY(-50%) rotate(-45deg);
}
@media (max-width: 479px) {
  .header-top .burger--open span:nth-child(3) {
    width: 24px;
  }
}
.header-top .burger--open span:nth-child(4) {
  transition: 0.2s all;
  opacity: 0;
}