
.characteristic-complectation {
  .characteristic-wraper {
    @media (max-width: 768px) {
      p {
        br {
          display: none;
        }
      }
    }

    @media (max-width: 576px) {
      grid-template-rows: auto auto 11px auto !important;
      gap: 20px !important;

      .media--img {
        img {
          width: 100%;
          max-height: none;
        }
      }
    }
  }
}
