
@import "../../scss/_global.scss";

.header-top {
  position: fixed;
  background-color: transparentize($gray, 1);
  width: 100%;
  box-shadow: 0 7px 9px 1px rgba(0, 0, 0, 0);
  transition: all 0.4s ease-in-out;
  padding: 0 20px;
  z-index: 20;
  top: 0;
  left: 50%;
  transform: translateX(-50%);

  @media (max-width: 576px) {
    padding: 0 10px;
  }

  .container {
    max-width: calc(1590px + 10px * 2);
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;

    @media (max-width: 992px) {
      max-width: calc(1590px + 10px * 2);
    }

    @media (max-width: 576px) {
      padding: 0 10px;
    }
  }

  &--scroll {
    background-color: $gray;
    box-shadow: 0 7px 9px 1px transparentize($gray, 0.7);

    .header-top__line {
      border-bottom: 1px solid transparentize($gray, 1);
    }
  }

  &__line {
    transition: all 0.4s ease-in-out 0.2s;
    max-width: calc(1764px + 20px * 2);
    border-bottom: 1px solid #fff;
    margin: 0 auto;

    .container {
      padding: 0;
    }
  }

  &__wrap {
    @include flex-center-x(16px);
    justify-content: space-between;
    height: 90px;

    @media (max-width: 576px) {
      height: 60px;
    }
  }

  &__logo {
    @include flex-center-x(30px);
    cursor: pointer;

    p {
      font-family: "IBM Plex Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      color: #fff;

      @media (max-width: 576px) {
        display: none;
      }
    }
  }

  &__contact {
    @include flex-center-x(30px);

    @media (max-width: 1280px) {
      gap: 20px;
    }

    @media (max-width: 768px) {
      gap: 14px;
    }

    &-phone {
      display: block;
      font-family: "IBM Plex Sans";
      font-style: normal;
      font-weight: 600;
      font-size: 22px;
      line-height: 29px;
      color: #fff;
      white-space: nowrap;
      transition: all 0.4s;

      &:hover {
        color: #e2bc85;
      }

      @media (max-width: 1366px) {
        display: none;
      }
    }

    &-button {
      cursor: pointer;
      @include flex-center-x(18px);
      height: 44px;
      width: 194px;
      justify-content: center;
      border: 1px solid #fff;
      border-radius: 22px;

      @media (max-width: 768px) {
        width: 44px;
      }

      @media (max-width: 576px) {
        width: 36px;
        height: 36px;
      }

      span {
        font-family: "IBM Plex Sans";
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 18px;
        color: #fff;
        white-space: nowrap;

        @media (max-width: 768px) {
          display: none;
        }
      }

      transition: all 0.3s;
      box-shadow: 0 10px 30px transparentize(#fff, 1);

      svg g path,
      span {
        transition: all 0.3s;
      }

      &:hover,
      &:focus {
        background: $main;
        transform: translateY(-1px) scale(1.03);
        border: 1px solid $main;
        box-shadow: 0 10px 30px transparentize($main, 0.78);

        svg g path,
        span {
          // fill: $gray;
          color: #fff;
        }
      }

      &:active {
        transform: translateY(1px);
      }
    }
  }

  &__nav {
    .menu-list {
      @include flex-center-x(35px);

      @media (max-width: 1280px) {
        flex-direction: column;
        align-items: start;
        gap: 10px;
        position: absolute;
        top: 0;
        z-index: 10;
        background: #fff;
        width: 280px;
        box-sizing: border-box;
        padding: 65px 20px 20px;
        right: 10px;
        transform: translateY(-100%);
        opacity: 0;
        transition: transform 0.45s 0.1s, opacity 0.25s;
      }

      @media (max-width: 576px) {
        top: -5px;
        right: 5px;
      }

      &--open {
        transform: translateY(10px);
        opacity: 1;
        transition: transform 0.4s, opacity 0.3s 0.2s;
      }

      li {
        p {
          font-family: "IBM Plex Sans";
          font-style: normal;
          font-weight: 600;
          font-size: 16px;
          line-height: 18px;
          color: #fff;
          transition: all 0.4s;
          cursor: pointer;

          &:hover {
            color: #e2bc85;
          }

          @media (max-width: 1280px) {
            color: $gray;
            padding: 5px 0;
            font-size: 18px;
          }
        }
      }
    }
  }

  .burger {
    &-container {
      @media (min-width: 1281px) {
        display: none;
      }

      display: block;
      height: 44px;
      width: 44px;
      border-radius: 22px;
      border: 1px solid #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 20;
      transition: all 0.3s;

      @media (max-width: 576px) {
        width: 36px;
        height: 36px;
      }

      &:hover,
      &:focus {
        transform: translateY(-1px) scale(1.03);
      }

      &:active {
        transform: translateY(1px);
      }

      &--open {
        border: 1px solid $gray;
      }
    }

    position: relative;
    width: 40px;
    height: 14px;

    span {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 20px;
      height: 2px;
      border-radius: 0;
      background-color: #fff;
      transition: 0.4s all;

      &:nth-child(1) {
        top: 0;
      }

      &:nth-child(2),
      &:nth-child(3) {
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
      }

      &:nth-child(4) {
        top: 100%;
        transform: translateX(-50%) translateY(-50%);
      }
    }

    &--open {
      span {
        background-color: $gray;
        &:nth-child(1) {
          transition: 0.2s all;
          opacity: 0;
        }

        &:nth-child(2) {
          transform: translateX(-50%) translateY(-50%) rotate(45deg);

          @media (max-width: 479px) {
            width: 24px;
          }
        }

        &:nth-child(3) {
          transform: translateX(-50%) translateY(-50%) rotate(-45deg);

          @media (max-width: 479px) {
            width: 24px;
          }
        }

        &:nth-child(4) {
          transition: 0.2s all;
          opacity: 0;
        }
      }
    }
  }
}
