#characteristic .characteristic-items {
  width: 100%;
  overflow: hidden;
  position: relative;
}
@media (max-width: 992px) {
  #characteristic .characteristic-items .characteristic-item-container {
    padding: 34px 34px 88px;
  }
}
@media (max-width: 576px) {
  #characteristic .characteristic-items .characteristic-item-container {
    padding: 24px 14px 88px;
  }
}
#characteristic .characteristic-navigation {
  display: none;
}
@media (max-width: 992px) {
  #characteristic .characteristic-navigation {
    display: flex;
    position: absolute;
    bottom: 104px;
    gap: 35px;
    left: 50%;
    z-index: 5;
    transform: translateX(-50%);
  }
}
@media (min-width: 993px) {
  #characteristic .swiper-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
  }
}
@media (min-width: 993px) and (max-width: 1280px) {
  #characteristic .swiper-wrapper {
    grid-template-rows: repeat(4, auto);
    align-items: center;
  }
}
@media (max-width: 992px) {
  #characteristic article.characteristic-item .characteristic-wraper__media {
    max-height: 350px;
  }
}
@media (max-width: 576px) {
  #characteristic article.characteristic-item .characteristic-wraper__media {
    max-height: 240px;
  }
}