@media (max-width: 768px) {
  .characteristic-complectation .characteristic-wraper p br {
    display: none;
  }
}
@media (max-width: 576px) {
  .characteristic-complectation .characteristic-wraper {
    grid-template-rows: auto auto 11px auto !important;
    gap: 20px !important;
  }
  .characteristic-complectation .characteristic-wraper .media--img img {
    width: 100%;
    max-height: none;
  }
}