
@import "../../scss/_global.scss";

.characteristic-size {
  @extend %indent-p;

  .title {
    gap: 30px;

    @media (max-width: 992px) {
      gap: 17px;
    }

    h2 {
      @include flex-center-x(35px);
      justify-content: center;

      @media (max-width: 992px) {
        flex-direction: column;
        font-weight: 700;
        font-size: 28px;
        line-height: 33px;
      }

      .block {
        display: inline-flex;
        align-items: center;
      }
      .quotes {
        font-weight: 500;
        font-family: "Roboto";
        font-size: 40px;
        line-height: 115%;
      }
      .title {
        font-weight: 700;
        font-size: 100px;
        line-height: 67px;
        font-family: "Phudu";

        @media (max-width: 992px) {
          font-size: 40px;
          line-height: 49px;
        }
      }
    }

    p {
      @media (max-width: 992px) {
        font-weight: 500;
        font-size: 22px;
        line-height: 29px;
      }
    }

    &.title-desktop {
      @media (max-width: 992px) {
        display: none;
      }
    }

    &.title-mobile {
      margin-bottom: 0;
      @media (min-width: 993px) {
        display: none;
      }
    }
  }

  .size-container {
    @extend %shadow-block;

    padding: 60px;
    @media (max-width: 1536px) {
      padding: 60px 40px;
    }
    @media (max-width: 1280px) {
      padding: 40px 30px;
    }
    @media (max-width: 992px) {
      padding: 34px;
    }
    @media (max-width: 576px) {
      padding: 24px 16px;
    }

    @media (max-width: 375px) {
      padding: 24px 14px;
    }
  }

  .size-wrapper {
    display: grid;
    position: relative;
    grid-template-columns: 2fr 3fr;
    row-gap: 40px;
    column-gap: 30px;

    @media (max-width: 1439px) {
      grid-template-columns: 1fr 1fr;
    }

    @media (max-width: 992px) {
      @include mobileContent();
      grid-template-columns: 1fr;
      gap: 35px;
    }

    &__img {
      grid-row: 1/4;
      grid-column: 1/2;
      width: 100%;
      align-self: center;

      img {
        width: 100%;
      }

      @media (max-width: 1439px) {
        grid-row: 2/3;
        grid-column: 1/2;

        img {
          max-width: 370px;
          display: block;
          margin: 0 auto;
        }
      }

      @media (max-width: 992px) {
        grid-column: 1/2;
        grid-row: 3/4;
      }

      @media (max-width: 425px) {
        img {
          max-height: 350px;
          width: auto;
        }
      }
    }

    &__char {
      grid-row: 2/3;
      grid-column: 2/3;

      @media (max-width: 1439px) {
        grid-row: 1/4;
        grid-column: 2/3;
        // align-self: center;
      }

      @media (max-width: 992px) {
        grid-column: 1/2;
        grid-row: 4/5;
      }
    }

    &__toggle {
      grid-row: 1/2;
      grid-column: 2/3;

      @media (max-width: 1439px) {
        grid-row: 1/2;
        grid-column: 1/2;
      }

      @media (max-width: 992px) {
        grid-column: 1/2;
        grid-row: 2/3;
      }
    }

    &__button {
      grid-row: 3/4;
      grid-column: 2/3;

      @media (max-width: 1439px) {
        grid-row: 3/4;
        grid-column: 1/2;
      }

      @media (max-width: 992px) {
        grid-column: 1/2;
        grid-row: 5/6;
      }

      .button {
        @extend %button;
        margin-left: auto;
        margin-right: 68px;
        @media (min-width: 1440px) and (max-width: 1536px) {
          margin-right: 0;
        }

        @media (max-width: 1439px) {
          margin: 0 auto;
        }
      }
    }
  }

  .char-wrap {
    display: flex;
    flex-direction: column;
    gap: 40px;

    @media (max-width: 1439px) {
      gap: 24px;
    }

    &__row {
      border-top: 1px solid $gray;
      display: flex;
      justify-content: space-between;
      gap: 25px;

      // @media (min-width: 993px) {
      //   &:last-child {
      //     width: max-content;
      //   }
      // }
      @media (max-width: 1439px) {
        flex-wrap: wrap;
        border-top: none;
        gap: 24px;

        .char-wrap__item {
          border-top: 1px solid $gray;

          @media (max-width: 992px) {
            border-top: 1px solid $gray;
          }
        }
      }

      @media (max-width: 992px) and (min-width: 769px) {
        justify-content: center;
      }
    }

    &__item {
      width: 420px;
      padding-top: 6px;

      @media (max-width: 1439px) {
        padding-top: 8px;

        width: 100%;
        max-width: 500px;
      }

      @media (max-width: 992px) {
        max-width: 100%;
      }

      h3 {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 115%;
        color: $gray;

        @media (max-width: 768px) {
          font-size: 20px;
          line-height: 23px;
          font-weight: 600;
        }
      }

      .profile-subtitle {
        font-weight: 400;
        font-size: 20px;
        line-height: 115%;
        font-family: "Roboto";
        font-style: normal;
        color: $gray;

        @media (max-width: 768px) {
          font-size: 16px;
          line-height: 19px;

          &__first {
            margin-top: 8px;
          }
        }

        span {
          font-weight: 700;
        }
      }

      p:last-child {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 115%;
        color: $gray;
        margin-top: 10px;

        span {
          font-weight: 700;
        }

        @include fluidFontSize(18, 20, 993, 1920);

        @media (max-width: 768px) {
          font-size: 16px;
          line-height: 19px;
        }
      }

      .profile-decription {
        min-height: 115px;

        @media (max-width: 1366px) {
          min-height: 89px;
        }
      }
    }
  }

  .toggle-container {
    display: flex;
    gap: 20px;
    width: max-content;
    margin-left: auto;

    @media (max-width: 1439px) {
      margin: 0 auto;
    }

    @media (max-width: 768px) {
      gap: 10px;
      width: 100%;
      justify-content: center;
    }

    .toggle-tab {
      background-color: white;
      width: 160px;
      height: 50px;
      outline: 3px solid transparent;
      outline-offset: -3px;
      border: 2px solid $gray;
      border-radius: 40px;
      transition: all 0.3s;
      position: relative;
      overflow: hidden;
      cursor: pointer;

      @media (max-width: 768px) {
        width: 100%;
        max-width: 130px;
        height: 40px;
      }

      &::before {
        @include before-blur();
      }

      span {
        position: absolute;
        z-index: 2;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);

        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 22px;
        line-height: 100%;
        color: $gray;

        @media (max-width: 768px) {
          font-weight: 400;
          font-size: 14px;
          line-height: 100%;
        }
      }

      &--active {
        border: 2px solid $main;
        background-color: $main;

        &:before {
          animation: sheen 1.2s forwards;
        }

        span {
          color: #ffffff;
        }
      }
    }
  }
}
