@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
#made {
  overflow: hidden;
}
#made h2 {
  font-size: calc(0.0164930556 * 100vw + 15.3333333333px);
  width: 102%;
  transform: translateX(-1%);
}
@media (min-width: 1921px) {
  #made h2 {
    font-size: 47px;
  }
}
@media (max-width: 768px) {
  #made h2 {
    font-size: 28px;
  }
}
@media (max-width: 768px) {
  #made .characteristic-item-container {
    position: relative;
    padding: 34px 34px 80px;
  }
}
@media (max-width: 768px) {
  #made .characteristic-item-container {
    position: relative;
    padding: 24px 14px 80px;
  }
}
@media (min-width: 769px) {
  #made .made-items {
    position: relative;
  }
}
#made .made-items .made-navigation {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 99%;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
}
@media (max-width: 768px) {
  #made .made-items .made-navigation {
    top: auto;
    bottom: 20px;
    transform: translateX(-50%);
    width: 130px;
  }
}
#made .made-items .swiper-slide {
  border-radius: 10px;
}
@media (max-width: 567px) {
  #made .characteristic-wraper {
    gap: 24px;
  }
}
#made .characteristic-wraper__media {
  overflow: hidden;
}
#made .characteristic-wraper__media img {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  display: block;
  border-radius: 10px;
}
@media (max-width: 1280px) {
  #made .characteristic-wraper__media img {
    width: 80%;
  }
}
@media (max-width: 425px) {
  #made .characteristic-wraper__media img {
    width: 100%;
  }
}
#made .characteristic-wraper__content p {
  padding-left: 27px;
  position: relative;
}
#made .characteristic-wraper__content p::before {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  content: "";
  background: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTciIGhlaWdodD0iMTQiIHZpZXdCb3g9IjAgMCAxNyAxNCIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTIgNy42MTExMUw2IDEyLjVMMTUgMS41IiBzdHJva2U9IiNFMkJDODUiIHN0cm9rZS13aWR0aD0iMyIgc3Ryb2tlLWxpbmVjYXA9InJvdW5kIiBzdHJva2UtbGluZWpvaW49InJvdW5kIi8+Cjwvc3ZnPgo=) no-repeat;
  height: 16px;
  width: 16px;
  background-size: 100%;
  background-position: center;
}
#made a {
  cursor: pointer;
  color: #2b2b2b;
  position: relative;
  display: inline;
  padding: 5px 0;
}
#made a::before, #made a::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 5px;
  display: block;
  width: 100%;
  height: 1px;
  background: #2b2b2b;
  transition: 1.1s cubic-bezier(0.19, 1, 0.22, 1);
}
#made a::before {
  transform: scaleX(0);
  transform-origin: left;
}
#made a::after {
  transform-origin: right;
  transition-delay: 0.25s;
}
#made a:hover::before {
  transform: scaleX(1);
  transition-delay: 0.25s;
}
#made a:hover::after {
  transform: scaleX(0);
  transition-delay: 0s;
}
#made .ref-container {
  margin-top: 27px;
  display: flex;
  justify-content: center;
}
@media (max-width: 768px) {
  #made .ref-container {
    display: none;
  }
}
#made .ref-container a {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 115%;
  font-size: calc(0.0021551724 * 100vw + 17.8620689655px);
}
@media (min-width: 1921px) {
  #made .ref-container a {
    font-size: 22px;
  }
}
@media (max-width: 992px) {
  #made .ref-container a {
    font-size: 20px;
  }
}