.tippy-box[data-theme~=green] {
  background: #468217 !important;
  border-radius: 20px;
  padding: 14px 16px;
  min-width: 404px !important;
  box-sizing: border-box;
}
@media (max-width: 768px) {
  .tippy-box[data-theme~=green] {
    display: none !important;
  }
}
.tippy-box[data-theme~=green] .tippy-content {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 120%;
  letter-spacing: 0.03em;
  color: #ffffff;
}
.tippy-box[data-theme~=green] .tippy-content span {
  display: block;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 31px;
  letter-spacing: 0.03em;
  margin-bottom: 8px;
  color: #ffffff;
}
@media (max-width: 768px) {
  .tippy-box[data-theme~=green] .tippy-content span {
    font-weight: 700;
    font-size: 20px;
    line-height: 31px;
  }
}
@media (max-width: 768px) {
  .tippy-box[data-theme~=green] .tippy-content {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
  }
}