svg {
  flex-shrink: 0;
}
svg.white circle {
  stroke: #fff;
}
svg.white path {
  fill: #fff;
}
@media (max-width: 768px) {
  svg.white {
    width: 24px;
    height: 24px;
  }
}
svg.green circle {
  stroke: #468217;
}
svg.green path {
  fill: #468217;
}
@media (max-width: 768px) {
  svg.green {
    width: 24px;
    height: 24px;
  }
}
svg.red circle {
  stroke: #d65555;
}
svg.red path {
  fill: #d65555;
}
@media (max-width: 768px) {
  svg.red {
    width: 24px;
    height: 24px;
  }
}
svg.red .vertical {
  display: none;
}
svg.white {
  filter: drop-shadow(0px 2px 5px rgba(255, 255, 255, 0));
  transition: all 0.8s;
}
@media (max-width: 768px) {
  svg.white {
    width: 30px;
    height: 30px;
  }
}
svg.white:hover {
  filter: drop-shadow(0px 2px 5px rgba(255, 255, 255, 0.6));
  transform: scale(1.05);
}