
.swiper-nav__container {
  z-index: 5;
  .swiper-btn__next,
  .swiper-btn__prev {
    cursor: pointer;

    svg {
      transition: all 0.6s ease;
    }
  }

  .swiper-btn__prev {
    &:active {
      svg {
        transform: translateY(3px);
      }
    }
  }

  .swiper-btn__next {
    svg {
      transform: rotate(180deg);
    }

    &:active {
      svg {
        transform: rotate(180deg) translateY(-3px);
      }
    }
  }
}
